import axios from 'axios';
import querystring from 'query-string';
const baseUrl = process.env.NODE_ENV !== 'production' ? 
                "http://www.easy-mock.com/mock/5a0cf3511e9be34076f9175f" :  "";
export  default async function request(url, options = {}) {
    let params = {},
        method = (options.method || 'get').toLowerCase(),
        data = options.data || {};
    params.url = url;
    params.method = method;
    params.baseURL = baseUrl;
    switch(method){
        case 'get' :
            params.params = data;
            break;
        case 'post' :
            params.data = querystring.stringify(data);
            break;
        case 'delete' :
            params.params = data;
            break;
        case 'put' :
            params.data = querystring.stringify(data);
            break;
    }
    let token = window.localStorage.getItem("token");
    if (token) {
        params.headers = { Authorization: token };
    }
    // catchmodel effect
    return await axios(params).catch(errHandle).then(parse);
}
function errHandle(res) {
    swal({
        title: '',
        text: JSON.stringify(res.message),
        icon: "error",
    });
    return;
}
function parse(response) {
    if (!response) throw "404";
    let result = response.data;
    if (!result) {
        throw "";
    }
    if (result.code == 404) {
        throw result;
    }
    if (result.errcode != 0) {
        throw result;
    }
    return result.data;
}


