import React, {Component, PropTypes} from 'react';
import styles from './form.less';

class Form extends Component {
    login = () => {
        const username = this.refs.username.value;
        const password = this.refs.password.value;
        if (!username || !password) {
            swal({
                title: '',
                text: "",
                icon: "error",
            });
            return;
        }
        this.props.loginActions({username, password});

    };
    render() {
        return (
            <div className={styles.page}>
                <div className={styles.login__form__bd}>
                    <h1 className={styles.login__title}>React</h1>
                    <div className={styles.login__username}>
                        <span></span>
                        <input type="text" ref="username"  label="" placeholder=""/>
                    </div>
                    <div className={styles.login__password}>
                        <span></span>
                        <input type="text" ref="password" label="" placeholder=""/>
                    </div>
                    <div className={styles.login__operator}>
                        <button onClick={this.login} className={styles.login__operator__in}></button>
                        <p className={styles.login__operator__register}></p>
                    </div>
                </div>
            </div>
        )
    }
}

Form.propTypes = {};
export default Form;




